.card {
    width: 100%;
    box-shadow: 8px 10px 35px -5px #272429;
    border-radius: 36px;
    background-color: rgb(252, 251, 250);
}

.cardName {
    padding: 0;
    font-size: 24px;
    margin-bottom: 10px;
    margin-top: 0;
}

.cardInfo {
    text-align: center;
}

@media  screen and (max-width: 768px) {
    .title {
        margin: 8px;
    }
}