.App {
  text-align: center;
  width: 100%;
  height: 100vh;
}

.pokemonCardContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
  gap: 20px;
  margin: 20px;
}
@media screen and (max-width: 768px) {
  .pokemonCardContainer {
    grid-template-columns: 1fr 1fr;
    margin: 10px;
  }
}

.btn {
  padding: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
@media screen and (max-width: 768px) {
  .btn {
    padding: 15px 0;
  }
}

.btn button {
  padding: 13px 32px;
  border: none;
  box-shadow: 8px 10px 35px -5px #272429;
  border-radius: 36px;
  background-color: rgb(62, 80, 243);
  color: white;
  cursor: pointer;
  transition: all 0.3s;
}
@media screen and (max-width: 768px) {
  .btn button {
    padding: 7px 16px;
  }
}

.btn button:hover {
  transform: translateY(5px);
  box-shadow: none;
}
